import VueParamaters from '@/components/VueParamaters/index.vue'
import MathJax3 from '@/components/MathJax3/index.vue'
import { syyType, shapeType, tfyType, jkdkbyType } from '@/hycom_lib/common'
import ParamExplain from './components/ParamExplain.vue'

@Component({
  components: {
    VueParamaters,
    MathJax3,
    ParamExplain
  }
})
export default class HycomForm extends Vue {
  @Prop() title!: string;
  @Prop() data!: Object;
  @Prop() initData!: Object;

  @Prop() demoContent!: string;
  @Prop() demoResult!: string;
  @Prop() formulas!: Object;

  public form = { result: '' };
  public syyOptions = [
    {
      label: '实用堰',
      value: syyType.syy
    },
    {
      label: '宽顶堰',
      value: syyType.kdy
    }
  ];

  public shapeOptions = [
    {
      label: '矩形断面',
      value: shapeType.RECTANGLE
    },
    {
      label: '梯形断面',
      value: shapeType.LADDER
    }
  ];

  public tfyTypeOptions = [
    {
      label: 'a型',
      value: tfyType.a
    },
    {
      label: 'b型',
      value: tfyType.b
    }
  ];

  public jkdkbyTypeOptions = [
    {
      label: '方角',
      value: jkdkbyType.fj
    },
    {
      label: '圆角',
      value: jkdkbyType.yj
    }
  ];

    public kechongOptions = [
      {
        label: '难冲',
        value: 0.8
      },
      {
        label: '可冲',
        value: 1.1
      },
      {
        label: '较易冲',
        value: 1.4
      },
      {
        label: '易冲',
        value: 1.8
      }
    ];

  public table221Options = [
    {
      label: '混凝土衬砌',
      options: [
        {
          value: '0.0115',
          label: '壁面顺直,有抹光的水泥浆面层或经磨光表面光滑者'
        },
        {
          value: '0.0125',
          label: '壁面顺直,采用钢模且拼接良好者'
        },
        {
          value: '0.0135',
          label: '壁面顺直,采用木摸拼接缝间凸凹度在3mm~5mm内者'
        },
        {
          value: '0.015',
          label: '壁面不够顺直,木摸拼接不良,缝间凸凹度在5mm~20mm者'
        },
        {
          value: '0.017',
          label: '粗糙的混凝土'
        }
      ]
    },
    {
      label: '喷混凝土',
      options: [
        {
          value: '0.0225',
          label: '岩石表面平整'
        },
        {
          value: '0.030',
          label: '岩石表面高低不平'
        }
      ]
    },
    {
      label: '喷浆护面',
      options: [
        {
          value: '0.0205',
          label: '喷浆护面'
        }
      ]
    },
    {
      label: '水泥浆砌块石护面',
      options: [
        {
          value: '0.0175',
          label: '渠底、壁面较顺直,砌石面较平整,拼接良好,1m2内不平整度约为30mm~50mm者'
        },
        {
          value: '0.025',
          label: '平整度较差'
        }
      ]
    },
    {
      label: '干砌块石或乱石护坡',
      options: [
        {
          value: '0.022',
          label: '渠底、壁面欠顺直,干砌石拼接一般'
        },
        {
          value: '0.029',
          label: '干砌块石平整度较差或乱石护坡'
        }
      ]
    },
    {
      label: '岩石',
      options: [
        {
          value: '0.025',
          label: '经过良好修整的'
        },
        {
          value: '0.0315',
          label: '经过中等修整的'
        },
        {
          value: '0.04',
          label: '未经修整,凸凹甚大者'
        }
      ]
    }
  ]

  public onDemo() {
    this.$emit('beforeOnDemo')
    for (let key in this.data) {
      (this.data as any)[key] = (this.initData as any)[key]
    }
  }

  public onReset() {
    this.$emit('beforeOnReset')
    for (let key in this.data) {
      if (typeof (this.data as any)[key] === 'boolean') {
        (this.data as any)[key] = false
      } else {
        (this.data as any)[key] = ''
      }
    }

    this.form.result = ''
  }

  public onCalculate() {
    this.$emit('beforeOnCalculate')
  }

  public CheckToken() {}
}
